body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  
}


.container {
    font-size: 40px;
    min-height: 500px;
    width: 400px;
    margin: auto;
    background: LightGray;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 20% 1fr;
    grid-gap: 10px;
    padding: 10px;
    grid-template-areas: 
        "display"
        "buttons";
}

#display { 
    background: pink;
    grid-area: display; 
}

#buttons {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 7px;
}

#clear { 
    background: powderblue;
    grid-column: 1 / 3;
}
#divide { background: rebeccapurple }
#multiply { background: salmon }
#seven { background: seashell }
#eight { background: skyblue }
#nine { background: teal }
#subtract { background: wheat }
#four { background: tomato }
#five { background: yellowgreen }
#six { background: steelblue }
#add { background: tan }
#one { background: thistle }
#two { background: peru }
#three { background: moccasin }
#equals { 
    background: mediumpurple;
    grid-column: 4 / 5;
    grid-row: 4 / 6;
}
#zero { 
    background: lightgreen;
    grid-column: 1 / 3;
}
#decimal { background: indianred }


